
body {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  backface-visibility: visible;
  background-size: cover;
  background-attachment: fixed;
  padding-top: 10rem;
  background-image: url(back3.jpg);
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}

body::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: -15rem;
  background-image: inherit;
  background-size: cover;
  background-attachment: fixed;
  filter: blur(5px); /* Adjust the blur radius as per your preference */
  z-index: -10;
}

/* Header */
.ui.fixed.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}

.ui.fixed.menu::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(to right, rgba(18, 18, 18, 0.503), rgba(246, 245, 241, 0.841), rgba(233, 231, 223, 0));
  animation: shimmer 2s infinite linear;
}

@keyframes shimmer {
  0% {
    background-position: -200%;
  }
  100% {
    background-position: 200%;
  }
}


.ui.container.center {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

